.main-title {
  background-image: url("../img/background-title.jpg");
  background-color: $lite-black;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.main-title__item {
  font-size: 24px;
  font-weight: 300;
  line-height: 33px;
  text-align: center;
  color: $white;
  padding: 106px 46px 51px 46px;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 50px;
    line-height: 68px;
  }
}
